.footer {
    padding-top: 57px;
    padding-bottom: 25px;
    background-color:$second-bg-color;
}
@media screen and (min-width: 768px) {
    .footer {
      padding-top: 79px;
      padding-bottom: 18px;
    }
    
}
@media screen and (min-width: 1280px) {
  .footer {
    padding-top: 82px;
    padding-bottom: 10px;
  }

}


.title-wrapper__suptitle {
    margin-bottom: 17px;
    padding-right: 66px;

    font-family: $second-font;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.16;
    text-transform: uppercase;
    color: $suptitle-color;
}

.title-wrapper__title { 
    margin-bottom: 43px;
    padding-right: 100px;

    font-family: $second-font;
    font-weight: 400;
    font-size: 34px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $color-accent;
}
@media screen and (min-width: 768px) {
  .title-wrapper__title {
    margin-bottom: 0;
  }

}
.contacts-wrapper {
    margin-bottom: 43px;
}
@media screen and (min-width: 768px) {
    .contacts-wrapper {
      margin-bottom: 0;
    }
    
}
.contacts-wrapper__list {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 46px;
}

.contacts-wrapper__item {
    &:not(:last-child) {
        margin-right: 28px;
    }
}

.contacts-wrapper__icon {
    width: 32px;
    height: 32px;
    fill: $suptitle-color;
    transition: fill 250ms $timing-function;

    .contacts-wrapper__link:hover &,
    .contacts-wrapper__link:focus & {
        fill: $color-accent;
    }
}

.footer-tel {
    display: flex;
    justify-content: flex-end;
}

.footer-tel__text {
    font-family: $main-font;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.8;
    color: $text-color-3;
}

.footer-tel__link {
    font-family: $main-font;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.8;
    color: $color-accent;
    transition: color 250ms $timing-function;

    &:hover,
    &:focus {
        color: #C7133E;
    }
}

.copyright {
    border-top: 1px solid #e1e1e1;
    padding-top: 22px;
    padding-left: 79px;
    padding-right: 91px;
}

.copyright__text {
font-family: $main-font;
font-weight: 700;
font-size: 14px;
line-height: 1.5;

text-align: center;
color: $text-color-2;
}

@media screen and (min-width: 768px) {

    .footer {
        padding-top: 82px;
    }

    .title-wrapper__suptitle {
        width: 340px;
        padding-right: 0px;
        font-size: 26px;
        line-height: 1.15;
        letter-spacing: 0.04em;
        margin-bottom: 16px;
    }

    .title-wrapper__title {
        width: 340px;
        padding-right: 0px;
        font-size: 48px;
        letter-spacing: 0.06em;
    }

    .footer .container {
        display: flex;
        justify-content: space-between;
        margin-bottom: 91px;
    }
    .contacts-wrapper__icon {
        width: 34px;
        height: 34px;
    }

    .footer-tel__text {
        font-size: 14px;
    }

    .footer-tel__link {
        font-size: 14px;
    }

    .copyright {
        padding-left: 246px;
        padding-right: 250px;
        padding-top: 29px;
    }

    .copyright__text {
        text-align: center;
        line-height: 1.8;
        letter-spacing: 0.02em;
    }
}

@media screen and (min-width: 1280px) {

    .footer .container {
        margin-bottom: 97px;
    }

    .title-wrapper__suptitle {
        width: 100%;
        font-size: 30px;
        line-height: 1.13;
    }

    .title-wrapper__title {
        width: 100%;
        font-size: 58px;
        line-height: 1.13;
    }

    .contacts-wrapper__list {
        margin-bottom: 50px;
    }

    .footer-tel__text {
        font-size: 16px;
        letter-spacing: 0.02em;
    }

    .footer-tel__link {
        font-size: 16px;
        letter-spacing: 0.02em;
    }

    .copyright {
        text-align: center;
        padding-top: 9px;
      }
}

   